
#result{padding:.6rem .8rem;width:auto;height:65%;background-color:#fff;border-radius:5px;text-align:left;overflow:auto;opacity:0;transition:600ms
}
#result.true{opacity:1;transition:600ms
}
#result ul{margin:0 0;padding:0 0
}
#result ul li{list-style:none
}
@media screen and (min-width:480px){
#result{padding:1rem 2rem
}
}


