
#container{margin:5% auto;width:auto;height:90%
}
#container .search{margin:auto;width:90%;height:100%;text-align:center
}
#container .search .search-input{padding:1rem .6rem;width:95%;font-size:1.2rem;color:#000;border:3px solid white;border-radius:5px;line-height:1em
}
#container .search .info{color:#fff;letter-spacing:.05em;opacity:.8
}
@media screen and (min-width:480px){
#container{max-width:768px
}
}


